import { useState } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

import TableOptions from '../tableOptions';
import MaskTable from '../maskTable';
import TabAbout from '../tabAbout';
// import TextareaData from '../textareadata';
import TabControl from '../tabControl';
import InterfaceSwitcher from '../interfaceSwitcher';
import AppConfig from '../../services/appConfig';

import './navMenu.css';

function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`wpglobus-to-tabpanel-${index}`}
      aria-labelledby={`vertical-wpglobus-to-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography component="div">{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `wpglobus-to-tab-${index}`,
    'aria-controls': `vertical-wpglobus-to-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
		height: '100%'
  },
	optionsBox: {
		height: '400px',
		width: '300px',
		float: 'left'
	},
	appBar: {
		backgroundColor: '#00c8f4', // #d3e4f4, '#d3e4f4', #a9b6c2, 
		color: '#000',
		width: '15%'
	},
	tabSelected: {
		fontWeight: '600'
	},
	tabHidden: {
		display: 'none'
	},
	tabsPanelWrapper: {
		display: 'flex'
	},
	tabsPanels60: {
		width: '60%'
	},
	tabsPanels80: {
		width: '80%'
	}
}));

export default function NavMenu() {
  const classes = useStyles();
  const [value, setValue] = useState(0);
	const config = AppConfig();

	const getTabPanelClasses = (index) => 'wpglobus-to-tabpanel wpglobus-to-tabpanel-'+index
	const getTabPanelWrapperClasses = (index) => index === 3 ? classes.tabsPanels80 : classes.tabsPanels60
	
	const isTabDisabled = (tab) => {
		let disabled = false;
		if ( tab === 'tabMasks' || tab === 'tabControl' ) {
			disabled = true;
		}
		return ( disabled && ! config.debug ) ? true : false
	}
	
	const getTabClasses = (index,extra='',tab='') => {
		let tabClasses = index === value ? classes.tabSelected + extra:extra;
		if ( tab !== '' && isTabDisabled(tab) ) {
			tabClasses = tabClasses + '' + classes.tabHidden;
		}
		return tabClasses;
	}

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

	let extraClass = '';

  return (
    <div className={classes.root}>
			<div className={classes.tabsPanelWrapper}>
				<AppBar position="static" className={classes.appBar}>
					<Tabs value={value} onChange={handleChange} aria-label="vertical-wpglobus-to-tabs tabs" orientation="vertical" indicatorColor="secondary">
						<Tab 
							label="About" 
							{...a11yProps(0)} 
							className={ getTabClasses(0) } 
						/>
						<Tab 
							label="Options to translate" 
							{...a11yProps(1)}
							className={ getTabClasses(1) } 
						/>
						<Tab 
							label="All options" 
							{...a11yProps(2)} 
							className={ getTabClasses(2) } 
						/>
						<Tab 
							label="Masks" 
							{...a11yProps(3)} 
							disabled={isTabDisabled('tabMasks')} 
							className={ getTabClasses(3, extraClass, 'tabMasks' ) } 
						/>
						<Tab 
							label="Control tab" 
							{...a11yProps(4)} 
							disabled={isTabDisabled('tabControl')} 
							className={ getTabClasses(4, extraClass, 'tabControl' ) } 
						/>
						<Tab 
							label="Interface v.1" 
							{...a11yProps(5)} 
							disabled={isTabDisabled('tabOldInterface')} 
							className={ getTabClasses(5, extraClass ) } 
						/>
					</Tabs>
				</AppBar>
				<div className={ getTabPanelWrapperClasses(value) }>
					<TabPanel value={value} index={0} className={ getTabPanelClasses(0) }>
						<TabAbout />
					</TabPanel>			
					<TabPanel value={value} index={1} className={ getTabPanelClasses(1) }>
						<TableOptions config={config} endpoint={'optionsToTranslate'} />
					</TabPanel>
					<TabPanel value={value} index={2} className={ getTabPanelClasses(2) }>
						<TableOptions config={config} endpoint={'allOptions'} />
					</TabPanel>
					<TabPanel value={value} index={3} className={ getTabPanelClasses(3) }>
						<MaskTable config={config} endpoint={'getMasks'} />
					</TabPanel>
					<TabPanel value={value} index={4} className={ getTabPanelClasses(4) }>
						<TabControl className={classes.optionsBox} />
					</TabPanel>
					<TabPanel value={value} index={5} className={ getTabPanelClasses(5) }>
						<InterfaceSwitcher 
							className={classes.optionsBox} 
							interfaceVersion={config.interfaceVersion} 
							optionKey={'interface_version_key'} 
							phase={config.phase} />
					</TabPanel>				
				</div>
			</div>
    </div>
  );
}
