import { useState, useEffect, useRef } from 'react';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';

// import TableContext from '../tableContext/tableContext';

import './scrollDialog.css';

export default function ScrollDialog(props) {
	
	// console.log('props:: ', props);
	
  const [open, setOpen] = useState(false);
  // const [scroll, setScroll] = useState('paper');
	const scroll = 'paper';
	
  // const handleClickOpen = (scrollType, viewOptionName, toggleOptionName) => () => {
  // const handleClickOpen = (scrollType, viewOptionName, toggleOptionName) => () => {
		// console.log('typeof toggleOptionName ::', typeof toggleOptionName);
		// toggleOptionName(viewOptionName);
		// console.log('scrollDialog.js viewOptionName ::', viewOptionName);
		// console.log('optionName ::', optionName);
    // setOpen(true);
    // setScroll(scrollType);
  // };

	if ( props.open ) {
		// setOpen(true)
	}
	
  const handleClose = () => {
		console.log( 'handleClose ::', '');
    setOpen(false);
  };

  const descriptionElementRef = useRef(null);
  useEffect(() => {
		// console.log('useEffect open ::', open);
    if (open) {
      const { current: descriptionElement } = descriptionElementRef;
      if (descriptionElement !== null) {
        descriptionElement.focus();
      }
    }
  }, [open]);

	// const toggleOptionName = (viewOptionName) => {
		// return viewOptionName;
	// }
	
	// const table = useContext(table);
	// console.log('table:: ', table.viewOption);
	// table.toggleViewOption(true)
	// setTimeout(function(){setOpen(true)}, 3000);
	
  // <Button onClick={handleClickOpen('body')}>scroll=body</Button>
  // <Button onClick={handleClickOpen('paper')}>scroll=paper</Button>
	// <Button className={"scroll-dialog-button"} onClick={handleClickOpen('paper', viewOptionName, toggleOptionName)}>scroll=paper</Button>
	
	// <Button onClick={handleClose}>Subscribe</Button>
	
			// <TableContext.Consumer>
				// {({viewOptionName, optionName, toggleOptionName}) => (
				// code
				// )}
			// </TableContext.Consumer>		
  return (
    <>
      <Dialog
        open={open}
        // open={toggleOptionName}
        onClose={handleClose}
        scroll={scroll}
        aria-labelledby="scroll-dialog-title"
        aria-describedby="scroll-dialog-description"
      >
        <DialogTitle id="scroll-dialog-title">Subscribe</DialogTitle>
        <DialogContent dividers={scroll === 'paper'}>
          <DialogContentText
            id="scroll-dialog-description"
            ref={descriptionElementRef}
            tabIndex={-1}
          >
            {[...new Array(50)]
              .map(
                () => `Cras mattis consectetur purus sit amet fermentum.
Cras justo odio, dapibus ac facilisis in, egestas eget quam.
Morbi leo risus, porta ac consectetur ac, vestibulum at eros.
Praesent commodo cursus magna, vel scelerisque nisl consectetur et.`,
              )
              .join('\n')}
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Cancel</Button>

        </DialogActions>
      </Dialog>
		
    </>
  );
}
